import React from "react";
import Card from "./Card";

const Cards = props => {
  const cardArray = props.robots.map((robot, i) => {
    return (
      <Card
        key={robot.id}
        id={robot.id}
        name={robot.name}
        email={robot.email}
      />
    );
  });
  return <div>{cardArray}</div>;
};

export default Cards;
